import React, { Component } from "react";

export default class Header extends Component {
    render() {

        const path = window.location.pathname;

        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container">
                    <a className="navbar-brand" href="#">Navbar</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto ml-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className={"nav-link " + (path=='/reactwebsite' ? 'active' : '')} aria-current="page" href="reactwebsite">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className={"nav-link " + (path=='/about' ? 'active' : '')} href="/about">About</a>
                            </li>
                            <li className="nav-item">
                                <a className={"nav-link " + (path=='/contact' ? 'active' : '')} href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        )
    }
}